export enum AircraftType {
    FixedWing = '固定翼飞行器',
    Helicopter = '直升机'
}

export enum PositionType {
    Airport = '机场',
    DisasterArea = '灾区',
    Source = '集结点',
    Destination = '安置点',
    Hospital = '医院',
}

export interface Aircraft {
    name: string,
    type: AircraftType,
    cruising_speed: number,
    max_fuel: number,
    fuel_consumption_per_unit_time: number
}
export interface Position {
    name: string,
    type: PositionType,
    longitude: number,
    latitude: number,
}