type RootGetters = {
    aircraftByName: (name: string) => Aircraft | undefined,
    positionByName: (name: string) => Position | undefined,
}

export function useStore() {
    const { state, getters, commit, dispatch } = baseUseStore(key)
    return {
        state,
        getters: getters as RootGetters,
        commit,
        dispatch
    }
}