import { createRouter, createWebHistory } from "vue-router";

export const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/SoSAirRescueHelper/',
            redirect: to => {
                return '/SoSAirRescueHelper/distance/single'
            }
        },
        {
            'path': '/SoSAirRescueHelper/distance/single',
            component: () => import('./components/MainPage.vue')
        },
        {
            'path': '/SoSAirRescueHelper/distance/multiple',
            component: () => import('./components/MultipleDistance.vue')
        }
    ]
})